.navigation {
    width: 100%;
    height: 6rem;
    background: $background-color;
    display: flex;
    align-items: center;
    padding: .5rem 5rem;
    padding-top: 3rem;
    // position: absolute;
    // top: 0;
    transform: translateY(0);
    z-index: map-get($z-index, navigation);
    box-shadow: none;
    @include bezier-transition(transform, .3s, ease);

  
    .logo {
      height: inherit;
      margin-right: 2rem;
    }
  
    .logo img {
      width: 5rem;
      height: inherit;
      object-fit: contain;
    }
  
    .logo a {
      display: block;
      height: 100%;
    }
  
    .searchbar {
      width: 300px;
    }
  }
  
  .navigation-admin {
    height: 6rem;
    background: $white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .05);
    padding: .5rem $l-size;
    display: flex;
    justify-content: space-between;
  }
  
  .navigation-menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0;
    margin: 0;
    text-align: right;
  }
  
  .navigation-menu-main {
    padding-left: 0;
    margin-right: 2rem;
    flex-grow: 1;
  
    li {
      display: inline-block;
      
      
      a {
        padding: 10px 15px;
        font-size: $font-small;
        opacity: .5;
        
        &:hover {
          background: $background-color-01;
        }
      }
    }
  }
  
  
  .navigation-action {
    display: flex;
    align-items: center;
    margin-left: $xxl-size;
  }
  
  
  .navigation-menu-active {
    font-weight: 300;
    opacity: 1 !important;
  }

  .navigation-menu-item {
    display: inline-block;
    list-style-type: none;
    padding: 0;
    margin: 0;

    &:hover {
      background: $background-color-01;
    }
  }